/* global */
$color-one: #ff9f1a;
$color-two: #302d2d;
$color-three: #4b4b4b;
$color-four: #ececec;

$font-family: 'Abril Fatface', cursive;

body {
    margin: 0;
    font-size: 16px;
    line-height: 1.7;
    font-family: 'Abril Fatface', cursive;
    color: #2c2c54; 
}
  
  .container {
    max-width: 1140px;
    padding: 0 20px;
    margin: 0 auto; 
}

/* navigation */
.navigation {
    padding: 12px 0;
}

.navigation ul {
    padding: 0;
    margin: 0;
    list-style-type: none;
    float: right;
}
.navigation li {
    display: inline-block;
    margin: 0 36px 0 0;
    position: relative;
}

.navigation a {
    text-decoration: none;
    color: $color-two;
    font-family: $font-family;
}

/* splash */

.btn {
    display: inline-block;
    min-width: 160px;
    margin: 24px auto 0 auto;
    padding: 16px;
    border-radius: 5px;
    font-family: $font-family;
    font-size: 16px;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    border-width: 5px;
    background-color: $color-two;
    background-position: 100% 0;
    background-size: 300%;
}

.splash {
    color: $color-two;
    background-image: url(images/budynek.jpg);
    min-height: 500px;
    display: flex;
}

.splash-inner {
    margin: 0;
    max-width: 70%;
}

.page-title {
    margin: 0;
    font-size: 42px;
    word-spacing: 4px;
    font-weight: normal;
    line-height: 1.2;
    font-family: $font-family;
    color: $color-four;
    
}

.page-subtitle {
    margin: 64px 0;
    font-size: 26px;
    font-weight: bold;
    font-family: $font-family;
}